@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&display=swap);
:root{--text-color: rgb(255, 255, 255);--bg-color: rgba(0, 0, 0, .5);--btn-bg-color: rgba(0, 0, 0, .8);--accent-color: #89119f;--body-font: "Poppins", sans-serif}.light-theme{--text-color: rgb(0, 0, 0);--bg-color: rgba(255, 255, 255, .5);--btn-bg-color: rgba(255, 255, 255, .8);--accent-color: #d77fe5}*,*::before,*::after{box-sizing:border-box;outline:none}*{padding:0;margin:0;border:0}ul{list-style:none}html{font-family:"Poppins", sans-serif;font-family:var(--body-font);background-image:url(https://4kwallpapers.com/images/wallpapers/macos-big-sur-apple-layers-fluidic-colorful-dark-wwdc-2020-5120x2880-1432.jpg);background-size:cover;background-position:center;font-size:25px}@media (max-width: 1366px){html{font-size:15px}}@media (max-width: 767px){html{font-size:14px}}body{color:rgb(255, 255, 255);color:var(--text-color);height:100vh;min-height:100vh;overflow:hidden}body.light-theme:before{content:"";position:absolute;left:0;top:0;right:0;bottom:0;background:linear-gradient(180deg, rgba(255,255,255,0.72) 0%, rgba(255,255,255,0.45) 100%);-webkit-backdrop-filter:saturate(3);backdrop-filter:saturate(3)}body.light-theme .dark-light svg{fill:transparent;stroke:rgb(255, 255, 255);stroke:var(--text-color)}body::-webkit-scrollbar{width:.5rem}body::-webkit-scrollbar-track{background-color:rgba(0, 0, 0, .5);background-color:var(--bg-color)}body::-webkit-scrollbar-thumb{background-color:rgb(255, 255, 255);background-color:var(--text-color)}@media (max-width: 767px){body{overflow-y:auto}}.dark-light{position:absolute;top:1rem;right:1rem;background-color:rgba(0, 0, 0, .8);background-color:var(--btn-bg-color);box-shadow:-1px 3px 8px -1px rgba(0,0,0,0.2);padding:8px;border-radius:50%;z-index:3;cursor:pointer}.dark-light svg{width:2rem;flex-shrink:0;fill:#ffce45;stroke:#ffce45;transition:0.5s}.container{max-width:80rem;width:100%;min-height:100vh;margin:0 auto;padding:1rem;display:flex;justify-content:center;align-items:center}

.header__new-todo{width:100%;padding:1rem;border:1px solid var(--accent-color);border-radius:1rem;background-color:transparent;color:inherit}

.todo-list{margin:2rem 0 0;height:14rem;overflow:auto;scrollbar-width:none}.todo-list li:not(:last-child){margin-bottom:.5rem}.todo-list::-webkit-scrollbar{width:.5rem}.todo{position:relative;display:flex;justify-content:space-between;font-size:1.5rem}@media (max-width: 479px){.todo{flex-direction:column}}.todo__part{display:flex;align-items:center}.todo__checkbox{position:absolute;left:-9999px}.todo__content{cursor:pointer}.todo__content:before{content:'';position:absolute;left:0;top:0;width:2rem;height:2rem;border:1px solid var(--accent-color);border-radius:.5rem;background-color:transparent}.todo__content:after{opacity:0;content:"";position:absolute;left:.5rem;top:.2rem;width:1rem;height:1rem;border-left:4px solid var(--accent-color);border-bottom:4px solid var(--accent-color);transform:rotate(-45deg);transition:all 0.2s ease}.todo__content_checked:after{opacity:1}.todo__content span{display:inline-block;max-width:35rem;overflow:hidden;text-overflow:ellipsis;padding-left:3rem}@media (max-width: 479px){.todo__content span{max-width:13rem}}.todo__color{margin-right:1rem;display:inline-block;width:7rem;padding:.5rem 1rem;background:transparent;border:1px solid var(--accent-color);border-radius:1rem;cursor:pointer;-webkit-appearance:none;-moz-appearance:none;appearance:none;background:url(../../static/media/arrow.d0ac216b.svg) no-repeat right 0.5rem center;background-size:1rem}.todo__deleteBtn{width:2rem;height:2rem;opacity:.5;cursor:pointer}.todo__deleteBtn:hover{opacity:1}.todo__deleteBtn:before,.todo__deleteBtn:after{position:absolute;right:1rem;content:'';height:2rem;width:.3rem;background-color:var(--accent-color)}.todo__deleteBtn:before{transform:rotate(45deg)}.todo__deleteBtn:after{transform:rotate(-45deg)}@media (max-width: 479px){.todo__deleteBtn{position:absolute;top:0;right:0}}

.footer{margin:1rem 0;display:flex}@media (max-width: 767px){.footer{flex-direction:column}}.footer__col{flex:1 1}.footer__col:not(:last-child){margin-right:1rem}.footer__title{font-size:1.5rem;margin-bottom:.5rem}.footer__todos-num{font-size:1.5rem}.footer__btn{max-width:12rem;width:100%;padding:.5rem 1.5rem;color:inherit;background:transparent;border:1px solid var(--accent-color);border-radius:1rem;cursor:pointer;transition:all .2s linear 0s}.footer__btn:not(:last-child){margin-bottom:1rem}.footer__btn:hover{background:var(--accent-color)}.footer__btn_selected{background:var(--accent-color)}.footer__filters li:not(:last-child){margin-bottom:.5rem}.footer__colors{display:flex;flex-direction:column}.footer__color{position:relative;display:flex;align-items:center;cursor:pointer}.footer__color:not(:last-child){margin-bottom:.4rem}.footer__color input{position:absolute;left:-9999px}.footer__color span{margin-right:.5rem;display:inline-block;width:2rem;height:1rem;border-radius:.2rem}.footer__color:before{margin-right:.5rem;content:'';display:inline-block;width:1.5rem;height:1.5rem;border:1px solid var(--accent-color);border-radius:.2rem}.footer__color:after{opacity:0;content:'';position:absolute;display:inline-block;width:.8rem;height:.8rem;left:.4rem;top:.2rem;border-left:3px solid var(--accent-color);border-bottom:3px solid var(--accent-color);transform:rotate(-45deg);transition:all .2s linear 0s}.footer__color_checked:after{opacity:1}

.app{position:relative;max-width:65rem;width:100%;display:flex;justify-content:center;background-color:var(--bg-color);border-radius:1rem;backdrop-filter:blur(1.3rem);-webkit-backdrop-filter:blur(1.3rem)}.app__section{width:80%;display:flex;flex-direction:column;align-items:center}.app__header{font-size:2rem;padding:1rem 0}.app__body{width:100%;display:flex;flex-direction:column}

